.come-in-main-div {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.come-loader {
  font-size: 48px;
  display: inline-block;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  color: #FF3D00;
  letter-spacing: 2px;
  position: relative;
}

.come-loader::after {
  content: 'Updated...Soon';
  position: absolute;
  left: 0;
  top: 0;
  color: #000000;
  width: 100%;
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
  animation: animloader 4s ease-in infinite;
}

@keyframes animloader {
  0% {
    width: 0%;
  }

  100% {
    width: 100%;
  }
}

/* __________tds fill________ */
.place-middle {
  min-height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
}